<template>
  <div>
    <div>
      <el-input size="small" v-model="jl.name" placeholder="..." prefix-icon="el-icon-plus"
                style="width: 300px"></el-input>

      <el-select v-model="jl.titleLevel" placeholder="" style="margin-left: 8px" size="small">
        <el-option
            v-for="item in titleLevels"
            :key="item"
            :label="item"
            :value="item">
        </el-option>
      </el-select>
      <el-button size="small" style="margin-left: 8px" icon="el-icon-plus" type="primary" @click="addJobLevel"></el-button>
    </div>

    <div style="margin-top: 10px" >
      <el-table
          :data="jls"
          stripe
          border
          size="small"
          style="width: 920px"
          @selection-change="handleSelectionChange">
        <el-table-column
            type="selection"
            width="55">
        </el-table-column>
        <el-table-column
            prop="id"
            label=""
            width="120">
        </el-table-column>
        <el-table-column
            prop="name"
            label=""
            width="150">
        </el-table-column>
        <el-table-column
            prop="titleLevel"
            label=""
            width="150">
        </el-table-column>
        <el-table-column
            prop="createDate"
            label=""
            width="150">
        </el-table-column>
        <el-table-column
            prop="enabled"
            label=""
            width="150">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.enabled"></el-tag>
            <el-tag type="danger" v-else ></el-tag>
          </template>
        </el-table-column>

        <el-table-column
            label=""
            width="150">
          <template slot-scope="scope">
            <el-button size="small" @click="showEditDialog(scope.row)"></el-button>
            <el-button size="small" type="danger" @click="deleteHandler(scope.row)"></el-button>
          </template>
        </el-table-column>

      </el-table>
    </div>
    <el-button type="danger" size="small" style="margin-top: 8px" :disabled="this.multipleSelection.length==0" @click="deleteMany"></el-button>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%">
      <table>
        <tr>
          <td>
            <el-tag></el-tag>
          </td>
          <td>
            <el-input v-model="updateJl.name" size="small" style="margin-left: 8px"></el-input>
          </td>
        </tr>
        <tr>
          <td>
            <el-tag></el-tag>
          </td>
          <td>
            <el-select v-model="updateJl.titleLevel" placeholder="" style="margin-left: 8px" size="small">
              <el-option
                  v-for="item in titleLevels"
                  :key="item"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </td>
        </tr>
        <tr>
          <td>
            <el-tag></el-tag>
          </td>
          <td>
            <el-switch style="margin-left: 8px"
                v-model="updateJl.enabled"
                active-color="#13ce66"
                inactive-color="#ff4949"
                active-text=""
                inactive-text="">
            </el-switch>

          </td>
        </tr>

      </table>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary"  @click="doUpdate()"> </el-button>
  </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: "JoblevelMana",
  mounted() {
    this.initJls();
  },
  data() {
    return {
      updateJl: {
        name:'',
        titleLevel: '',
        enabled: ''
      },
      dialogVisible: false,
      jls: [],
      titleLevels: [
        '', '', '', '', ''
      ],
      jl: {
        name: '',
        titleLevel: ''
      },
      multipleSelection: []
    }
  },
  methods: {
    deleteMany() {
      this.$confirm('[' + this.multipleSelection.length + '], ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let ids = '?';
        this.multipleSelection.forEach(item=>{
          ids += 'ids=' + item.id + '&';
        })
        this.deleteRequest('/system/basic/joblevel/'+ids).then(resp => {
          if (resp) {
            this.initJls();
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;

    },
    doUpdate() {
      this.putRequest('/system/basic/joblevel/',this.updateJl).then(resp=>{
        if (resp) {
          this.initJls();
          this.dialogVisible = false;
        }
      })
    },
    showEditDialog(data) {
      data.createDate = '';
      Object.assign(this.updateJl, data);
      this.dialogVisible = true;
    },
    deleteHandler(data) {
      this.$confirm('[' + data.name + '], ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteRequest('/system/basic/joblevel/'+data.id).then(resp => {
          if (resp) {
            this.initJls();
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    addJobLevel() {
      if (this.jl.name && this.jl.titleLevel) {
        this.postRequest('/system/basic/joblevel/',this.jl).then(resp=>{
          if (resp) {
            this.initJls();
          }
        })
      }else {
        this.$message("");
      }
    },
    initJls() {
      this.getRequest('/system/basic/joblevel/').then(resp => {
        if (resp) {
          this.jls = resp;
          this.jl.name = '';
          this.jl.titleLevel = '';
        }
      })
    }
  }
}
</script>

<style scoped>

</style>