<template>
  <div>
    <div style="display: flex;justify-content: space-between">
      <el-button type="primary" icon="el-icon-plus" @click="showAddSalaryView"></el-button>
      <el-button type="success" icon="el-icon-refresh"></el-button>

    </div>

    <div style="margin-top: 8px">
      <el-table

          v-loading="loading"
          element-loading-text=""
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"

          :data="salaries"
          border
          stripe>
        <el-table-column
            type="selection"
            width="40">
        </el-table-column>

        <el-table-column
            prop="name"
            label=""
            width="120">
        </el-table-column>
        <el-table-column
            prop="basicSalary"
            label=""
            width="80">
        </el-table-column>
        <el-table-column
            prop="lunchSalary"
            label=""
            width="80">
        </el-table-column>
        <el-table-column
            prop="trafficSalary"
            label=""
            width="80">
        </el-table-column>
        <el-table-column
            prop="bonus"
            label=""
            width="80">
        </el-table-column>
        <el-table-column
            prop="createDate"
            label=""
            width="100">
        </el-table-column>
        <el-table-column
            align="center"
            label="">
          <el-table-column
              prop="pensionPer"
              label="">
          </el-table-column>
          <el-table-column
              prop="pensionBase"
              label="">
          </el-table-column>
        </el-table-column>
        <el-table-column
            align="center"
            label="">
          <el-table-column
              prop="accumulationFundPer"
              label="">
          </el-table-column>
          <el-table-column
              prop="accumulationFundBase"
              label="">
          </el-table-column>
        </el-table-column>
        <el-table-column
            align="center"
            label="">
          <el-table-column
              prop="medicalPer"
              label="">
          </el-table-column>
          <el-table-column
              prop="medicalBase"
              label="">
          </el-table-column>
        </el-table-column>
        <el-table-column
            label=""
            width="150">
          <template slot-scope="scope">

            <el-button type="primary" @click="showEditdialog(scope.row)" ></el-button>
            <el-button type="danger" @click="delSalary(scope.row)"></el-button>

          </template>
        </el-table-column>




      </el-table>
    </div>

    <el-dialog
        :title="diagTitle"
        :visible.sync="dialogVisible"
        width="50%">
      <div style="display: flex;justify-content: space-around;align-items: center">
        <el-steps direction="vertical" :active="activeItemIndex">
          <el-step :title="itemName" v-for="(itemName,index) in salaryItemName"  :key="index"></el-step>
        </el-steps>

        <el-input :placeholder="''+salaryItemName[index]" v-for="(value,title,index) in salary" v-model="salary[title]"   :key="index" v-show="activeItemIndex==index" style="width: 200px"></el-input>

      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="preStep">{{activeItemIndex==0||activeItemIndex==10?'':''}}</el-button>
        <el-button type="primary" @click="nextStep">{{activeItemIndex==10?'':''}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "SalSob",
  data() {
    return {
      loading:true,
      diagTitle: '',
      activeItemIndex: 0,
      salaryItemName: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
      ],
      dialogVisible: false,
      salaries: [],
      salary: {
        name: '',
        basicSalary: 0,
        trafficSalary: 0,
        lunchSalary: 0,
        bonus: 0,
        pensionPer: 0,
        pensionBase: 0,
        medicalPer: 0,
        medicalBase: 0,
        accumulationFundPer: 0,
        accumulationFundBase: 0

      }
    }
  },
  mounted() {
    this.initSalaries();
  },
  methods: {
    showEditdialog(data) {
      this.diagTitle = '';
      this.activeItemIndex=0;
      this.salary.id = data.id;
      this.salary.name = data.name;
      this.salary.basicSalary = data.basicSalary;
      this.salary.trafficSalary = data.trafficSalary;
      this.salary.lunchSalary = data.lunchSalary;
      this.salary.bonus = data.bonus;
      this.salary.pensionPer = data.pensionPer;
      this.salary.pensionBase = data.pensionBase;
      this.salary.medicalPer = data.medicalPer;
      this.salary.medicalBase = data.medicalBase;
      this.salary.accumulationFundPer = data.accumulationFundPer;
      this.salary.accumulationFundPer = data.accumulationFundPer;
      this.dialogVisible = true;
    },
    delSalary(salary) {
      this.$confirm('[' + salary.name + '], ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteRequest('/salary/sob/' + salary.id).then(resp => {
          if (resp) {
            this.initSalaries();
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    preStep() {
      if (this.activeItemIndex == 0) {
        this.dialogVisible = false;
        return
      }
      if (this.activeItemIndex == 10) {
        this.activeItemIndex = 0;
        this.dialogVisible = false;
        return;
      }
      this.activeItemIndex--;
    },
    nextStep() {
      if (this.activeItemIndex == 10) {
        if (this.salary.id) {
          this.putRequest('/salary/sob/', this.salary).then(resp => {
            if (resp) {
              this.dialogVisible = false;
              this.activeItemIndex = 0;
              this.initSalaries();
            }
          });
        } else {
          this.postRequest('/salary/sob/', this.salary).then(resp => {
            if (resp) {
              this.dialogVisible = false;
              this.initSalaries();
            }
          });
        }
        return;
      }
      this.activeItemIndex++;
    },
    showAddSalaryView() {
      this.activeItemIndex = 0;
      this.salary = {
        name: '',
        basicSalary: 0,
        trafficSalary: 0,
        lunchSalary: 0,
        bonus: 0,
        pensionPer: 0,
        pensionBase: 0,
        medicalPer: 0,
        medicalBase: 0,
        accumulationFundPer: 0,
        accumulationFundBase: 0
      };
      this.dialogVisible = true;
    },
    initSalaries() {
      this.getRequest('/salary/sob/').then(resp => {
        if (resp) {
          this.salaries = resp;
          this.loading=false;
        }
      });
    }
  }
}
</script>

<style scoped>

</style>